form {
  text-align: center;
  margin: 20px auto;
}

input[type="text"] {
  width: 100%;
  max-width: 400px;
  border: 1px solid #ccc;
  border-radius: 50px;
  padding: 10px;
  font-size: 16px;
}

.input:focus {
  transform: scale(1.05);
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 50px;
  padding: 10px 20px;
  font-size: 16px;
}

.btn:hover, .btn:focus {
  background-color: #6f00ff;
  transition: all .25s;
  transform: scale(1.05);
}

#imageGallery {
  max-width: 800px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin: 20px auto;
  display: flex;
}

#imageGallery img {
  max-width: 100%;
  height: auto;
  border: 1px solid #ddd;
  border-radius: 4px;
  transition: transform .2s ease-in-out;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

#imageGallery img:hover {
  transform: scale(1.05);
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 50px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

.load-more:disabled {
  cursor: not-allowed;
  background-color: #ccc;
}

